
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'


export default {
components: {
Tab,
TabItem
},
name: '',
data() {
return {
index01: 0,
currentList: [],
avatar_url: '',
userListWeek:[],
userListTotal:[],
total: false,
Week: false,
defaultImg: 'this.src="' + require('../assets/images/headicon.png') + '"',
}
},
methods: {
switchTabItem(index) {

switch (index){
case 0:{
if(!this.total) {
this.getDataTotal()
}
this.currentList = this.userListWeek
break
}
case 1: {
if(!this.total) {
this.getDataTotal()
}
this.currentList = this.userListTotal
break
}
}
this.index01 = index
},
getDataTotal() {

let data = [
{
userinfo: {
sex: 1,
avatar_thumb: '',
user_nicename: 'rosalind',
level: 19
},
total: 12345
},
{
userinfo: {
sex: 1,
avatar_thumb: '',
user_nicename: 'rosalind2',
level: 23
},
total: 1233
},
{
userinfo: {
sex: 0,
avatar_thumb: '',
user_nicename: 'rosalind3',
level: 45
},
total: 12
},
]
this.userListTotal = data
this.total = true
},
getDataWeek() {
let data = [
{
userinfo: {
sex: 0,
avatar_thumb: '',
user_nicename: '萌萌',
level: 12
},
total: 78945
},
{
userinfo: {
sex: 1,
avatar_thumb: '',
user_nicename: '萌萌2',
level: 12
},
total: 12345
},
{
userinfo: {
sex: 0,
avatar_thumb: '',
user_nicename: '萌萌3',
level: 89
},
total: 12
},
]
this.week = true
this.userListWeek = data
}
},
created() {
this.getDataWeek()
this.currentList = this.userListWeek
}
}



